.h-wrapper{
    background-color: var(--black);
    color: white;
}
.h-container{
    justify-content: space-between;
    padding: 1rem 0.5rem;
    color: var(--secondary);
}
.h-menu{
    gap: 2rem;
}

.h-menu>*:hover{
    cursor: pointer;
}
.menu-icon{
    display: none;
}

@media screen and (max-width : 768px) {
    .menu-icon{
        display: block;
    }
    .h-menu{
        color: black;
        position: absolute;
        top: 3rem;
        right: 4rem;
        background-color: white;
        flex-direction: column;
        font-weight: 600;
        padding: 2rem;
        z-index: 999;
        border-radius: 10px;
        align-items: flex-start;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
        transition: all 300ms ease-in;
    }
}