.v-container .image-container{
border: 8px solid rgb(232, 232, 232 / 93%);
}
.v-container>div{
    flex: 1;
}
.v-right {
    gap: 0.5rem;
}
.accordion{
    margin-top: 2rem;
    border: none;
}
.accordionItem{
    background-color: white;
    border: 0.8px solid rgba(128,128,128, 0.143);
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 20px;
    padding: 10px;

}
.accordionButton{
    background-color: white;
    padding: 1rem;
    width: 100%;
    justify-content: space-between;
    cursor: pointer;
}
.icon{
    padding: 10px;
    background-color: #eeeeff;
    border-radius:5px ;
}
.icon svg{
    fill: var(--blue);
}
.accordionItem.expanded{
    box-shadow: var(--shadow);
    border-radius: 6px;
}
@media screen and (max-width:1000px) {
    .v-container{
        flex-direction: column;
    }
}
